* {
margin: 0;
padding: 0;
-webkit-box-sizing: border-box;
        box-sizing: border-box;
font-family: 'Roboto', serif; }

html::-webkit-scrollbar {
width: 8px;
background-color: #73C8A9; }

html::-webkit-scrollbar-thumb {
background-color: #ffffff;
border-radius: 10px; }

body {
background: #73C8A9;
background: -webkit-linear-gradient(to right, #373B44, #73C8A9);
background: -webkit-gradient(linear, left top, right top, from(#373B44), to(#73C8A9));
background: -webkit-linear-gradient(left, #373B44, #73C8A9);
background: -o-linear-gradient(left, #373B44, #73C8A9);
background: linear-gradient(to right, #373B44, #73C8A9); }

.divide {
border: 0.5px solid #dddddd;
width: 50%;
margin: 0 auto;
position: relative;
top: 20px; }

.container {
background: #ffffff;
width: 40%;
height: auto;
margin: 50px auto;
padding: 50px;
-webkit-box-shadow: 0px -12px 18px -9px rgba(0, 0, 0, 0.75);
box-shadow: 0px -12px 18px -9px rgba(0, 0, 0, 0.75); }

.container::after {
clear: both;
content: "";
display: block; }

.header {
text-align: center;
margin-top: 30px;
}

.myself {
width: 220px;
height: 220px;
}

.title {
font-weight: 700;
margin-top: 30px;
}

.subtitle {
color: #999999;
margin-top: 10px;
}

.social {
margin: 20px auto;
text-align: center;
}

.social i {
font-size: 25px;
margin-right: 10px;
color: #dddddd;
}

.social i:hover {
color: #000000;
}

.container .content {
margin-top: 50px; }
.container .content .row {
margin-bottom: 40px; }
.container .content .work h4 {
color: #494C4E;
font-weight: 700; }
.container .content .work .l-response {
display: none; }
.container .content .work p {
margin-top: 20px;
font-size: 16px; }
.container .content .work p a {
text-decoration: none; }
.container .content .work p a:hover {
text-decoration: underline; }
.container .content .work p span {
font-weight: 700;
color: #494C4E; }
.container .content p {
line-height: 1.2;
color: #494C4E;
opacity: 0.8;
font-size: 17px;
-webkit-font-smoothing: antialiased; }
.container footer {
margin-top: 60px;
text-align: center;
font-size: 14px;
color: #999;
font-weight: 700; }
.container footer p {
font-size: 18px;
position: relative;
top: 20px;
}

@media screen and (max-width: 768px) {
    .container {
      width: 80%;
      padding: 30px; }
      .container header {
        margin-top: 15px; }
        .container header p {
          font-size: 16px; }
        .container header .myself {
          width: 170px;
          height: 170px; }
      .container .content .work .l-response {
        display: inline-block;
        font-size: 16px;
        margin-top: 5px; }
        .container .content .work .l-response i {
          color: #999999; }
      .container footer {
        margin-top: 30px; }
        .container footer p {
          top: 8px !important; } }





